.totas-box{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  z-index: 9999;
}
.totas-text{
  font-size: 0.7273rem;
  color: #fff;
  background: rgba(0,0,0,.8);
  border-radius: 0.4545rem;
  padding: 0.4545rem 0.9rem;
  display: inline-block;
  max-width: 50%;
  margin: auto;
}
.totas-box.show{
  transform: translateY(0);
}
.totas-box.hide{
  transform: translateY(-100%);
  transition-delay: 1s;
}
.totas-box.show .totas-text{
  transform: scale(1);
  transition-duration: 0.5s;
}
.totas-box.hide .totas-text{
  transform: scale(0);
  transition-duration: 1s;
}
